import {
  TextField,
  Button,
  FormControlLabel,
  Box,
  Alert,
  Checkbox,
} from "@mui/material";
import { useState } from "react";

const Registration = () => {
  const [error, setError] = useState({
    status: false,
    msg: "",
    type: "",
  });
  const handleSubmit = (e) => {
    e.preventDefault();
    const data = new FormData(e.currentTarget);
    const actualData = {
      name: data.get("name"),
      name2: data.get("name2"),
      email: data.get("email"),
      password: data.get("password"),
      password_confirmation: data.get("password_confirmation"),
      tc: data.get("tc"),
    };
    if (
      actualData.name2 &&
      actualData.name &&
      actualData.email &&
      actualData.password &&
      actualData.password_confirmation &&
      actualData.tc !== null
    ) {
      if (actualData.password === actualData.password_confirmation) {
        console.log(actualData);
        document.getElementById("registration-form").reset();
        setError({
          status: true,
          msg: "registration successfull",
          type: "success",
        });
      } else {
        setError({
          status: true,
          msg: "password and confirm password does not match",
          type: "error",
        });
      }
    } else {
      setError({ status: true, msg: "All fields are required", type: "error" });
    }
  };
  return (
    <>
      <Box
        component="form"
        noValidate
        sx={{ mt: 1.5, ml: 1, mr: 1 }}
        id="registration-form"
        onSubmit={handleSubmit}
      >
        <TextField
          margin="normal"
          required
          fullWidth
          id="name"
          name="name"
          label="First-Name"
        />
        <TextField
          margin="normal"
          required
          fullWidth
          id="name2"
          name="name2"
          label="Last-Name"
        />
        <TextField
          margin="normal"
          required
          fullWidth
          id="email"
          name="email"
          label="Email Address"
        />
        <TextField
          margin="normal"
          required
          fullWidth
          id="password"
          name="password"
          label="Password"
          type="password"
        />
        <TextField
          margin="normal"
          required
          fullWidth
          id="password_confirmation"
          name="password_confirmation"
          label="Confirm-Password"
          type="password"
        />
        <FormControlLabel
          control={<Checkbox value="agree" name="tc" id="tc" />}
          label="I agree to all term and condition"
        />
        <Box textAlign="center">
          <Button type="submit" variant="contained" sx={{ mt: 2 }}>
            join
          </Button>
        </Box>
        {error.status ? <Alert severity={error.type}>{error.msg}</Alert> : ""}
      </Box>
    </>
  );
};

export default Registration;
