import { Grid, Card, Typography, Tabs, Tab, Box } from "@mui/material";
import { borderBottom } from "@mui/system";
import { useState } from "react";
import shoping from "../../images/shoping.png";
import Registration from "./Registration";
import UserLogin from "./UserLogin";

const TabPanel1 = (props) => {
  const { children, value, index } = props;
  return (
    <div role="TabPanel1" hiden={value !== index}>
      {value === index && <Box>{children}</Box>}
    </div>
  );
};

const LoginReg = () => {
  const [value, setValue] = useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
      <Grid container sx={{ height: "90vh" }}>
        <Grid
          item
          lg={7}
          sm={5}
          sx={{
            backgroundImage: `url(${shoping})`,
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
            backgroundPosition: "center",
            display: { xs: "none", sm: "block" },
          }}
        ></Grid>

        {/* right side pannel */}
        <Grid item lg={5} sm={7} xs={12}>
          <Card sx={{ width: "100%", height: "100%" }}>
            <Box>
              <Box sx={{ borderBottom: 1, borderColor: "secondary" }}>
                <Tabs
                  value={value}
                  textColor="secondry"
                  onChange={handleChange}
                >
                  <Tab label="Login" sx={{ textTransform: "none" }}></Tab>
                  <Tab
                    label="Registration"
                    sx={{ textTransform: "none" }}
                  ></Tab>
                </Tabs>
              </Box>
              <TabPanel1 value={value} index={0}>
                <UserLogin />
              </TabPanel1>
              <TabPanel1 value={value} index={1}>
                <Registration />
              </TabPanel1>
            </Box>
          </Card>
        </Grid>
      </Grid>
    </>
  );
};

export default LoginReg;
