import { AppBar, Box, Toolbar, Typography, Button } from "@mui/material";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <>
      <Box sx={{ flexGrow: 1 }} />
      <AppBar position="static" color="secondary">
        <Toolbar>
          <Typography variant="h5" component="div" sx={{ flexGrow: 1 }}>
            this is a toolbar
          </Typography>
          <Button
            color="inherit"
            style={({ isActive }) => {
              return { backgroundColor: isActive ? "#2196f3" : " " };
            }}
            component={NavLink}
            to="/"
          >
            Home
          </Button>
          <Button
            color="inherit"
            style={({ isActive }) => {
              return { backgroundColor: isActive ? "#2196f3" : " " };
            }}
            component={NavLink}
            to="/contact"
          >
            contact
          </Button>
          <Button
            color="inherit"
            style={({ isActive }) => {
              return { backgroundColor: isActive ? "#2196f3" : " " };
            }}
            component={NavLink}
            to="/login"
          >
            Login/Registration
          </Button>
        </Toolbar>
      </AppBar>
    </>
  );
};

export default Navbar;
